.home-menu {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;

    .menu-heading{
        .menu-title {
            font-size: 2rem;
            margin-bottom: 1rem;
            font-weight: bold;
            margin-bottom: 1rem;
        
            span {
              color: #ff6200; // Orange color
            }
        }
    }

    .menu-item {
        width: calc(25% - 20px); // Four items per row
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        text-align: center;
        background: white;
        border-radius: 10px;
        overflow: hidden;

        img {
            width: 100%; /* Ensures the image fills its container */
            height: 200px; /* Adjust height as needed for uniformity */
            object-fit: cover; /* Ensures images maintain aspect ratio and cover the container */
            border-bottom: 1px solid #ddd;
        }

        h3 {
            margin: 10px 0;
            font-size: 1.2rem;
            color: #333;
        }

        p {
            font-size: 1rem;
            color: #777;
        }
    }

    @media (max-width: 769px) {
        .menu-item {
            width: calc(50% - 20px); /* Two items per row on smaller screens */
        }
    }
}
