/* footer.scss */
section{
    padding-top: 45px;
}

.footer {
    background-color: #333;
    color: #fff;
    padding: 40px 20px;
    
    
    .footer-content {
      display: flex;
      justify-content: space-between;
      
      @media (max-width: 769px) {
        flex-direction: column;
      }
      
      .footer-column {
        flex: 1;
        margin-right: 20px;
        //justify-content: space-around;
        .subscribe{
            border-radius: 0.5rem;
            //padding-right: 12px;
            padding: .2rem 1.5rem;
            //justify-content: space-around;
            border: 12px;
        }
        
        &:last-child {
          margin-right: 0;
        }
        
        h3 {
          font-size: 1.5rem;
          margin-bottom: 10px;
        }
        
        p {
          font-size: 1rem;
          margin-bottom: 5px;
        }
        
        .icons {
          margin-top: 10px;

          .icon{
            &:hover{
                color: orangered;
                transition: 0.5s;
            }
          }
          
          svg {
            font-size: 1.5rem;
            margin-right: 10px;
          }
        }
      }
    }
  }
  