.about-container {
    text-align: center;
    padding: 20px;
    background-color: #fff5e6; // Light orange background for the container
  
    .about-heading {
      font-size: 2.5rem;
      color: #000000; // Orange color for the heading
      margin-bottom: 20px;
      span{
        color: #ff6600;
      }
    }
  
    .about-content {
      font-size: 1.2rem;
      color: #333;
      max-width: 800px;
      margin: 0 auto;
      
      p {
        margin-bottom: 20px;
      }
  
      .about-images {
        display: flex;
        flex-direction: column;
        gap: 20px;
  
        .about-image {
          width: 100%;
          height: auto;
          border-radius: 10px;
          box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        }
      }
    }
  }
  
  @media (min-width: 769px) {
    .about-content {
      .about-images {
        flex-direction: row;
        justify-content: space-between;
  
        .about-image {
          width: 48%;
        }
      }
    }
  }
  