.about-container {
  text-align: center;
  padding: 20px;
  background-color: #fff5e6;
}
.about-container .about-heading {
  font-size: 2.5rem;
  color: #000000;
  margin-bottom: 20px;
}
.about-container .about-heading span {
  color: #ff6600;
}
.about-container .about-content {
  font-size: 1.2rem;
  color: #333;
  max-width: 800px;
  margin: 0 auto;
}
.about-container .about-content p {
  margin-bottom: 20px;
}
.about-container .about-content .about-images {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.about-container .about-content .about-images .about-image {
  width: 100%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

@media (min-width: 769px) {
  .about-content .about-images {
    flex-direction: row;
    justify-content: space-between;
  }
  .about-content .about-images .about-image {
    width: 48%;
  }
}/*# sourceMappingURL=about.css.map */