.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 85vh;
  background-color: #f0f0f0; /* Fallback background color */
  overflow: hidden;
}

.banner {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden; /* Ensure image and overlay stay within container */
}

.banner-image {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover; /* Ensure image covers entire container */
  filter: brightness(70%); /* Darken the image for better readability */
}

.text-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff; /* White text for better contrast */
  text-align: center;
  width: 80%;
  max-width: 600px; /* Limit the maximum width of the text overlay */
}

.image-title {
  font-size: 3rem;
  margin-bottom: 20px;
}

.image-description {
  font-size: 1.5rem;
  line-height: 1.6;
}

@media screen and (max-width: 768px) {
  .text-overlay {
    width: 90%;
  }
}/*# sourceMappingURL=main.css.map */