.featured {
    text-align: center;
    padding: 2rem 0;
  
    .featured-title {
      font-size: 2rem;
      margin-bottom: 1rem;
      font-weight: bold;
  
      span {
        color: #ff6200; // Orange color
      }
    }
  
    .carousel {
      overflow: hidden;
      width: 80%;
      margin: 0 auto;
  
      .carousel-inner {
        display: flex;
        transition: transform 1.5s ease-in-out;
        width: calc(100% * 2); // Adjust width for 8 images, showing 4 at a time
      }
  
      .carousel-item {
        flex: 0 0 calc(25% - 10px); // 4 images per slide, with separation
        margin: 0 5px; // 5px margin on left and right
        box-sizing: border-box; // Include padding and border in the element's total width and height
  
        img {
          width: 100%;
          height: 100%;
          object-fit: cover; // Ensure images cover the entire space without distortion
        }
      }
    }
  
    .carousel-dots {
      margin-top: 1rem;
  
      .dot {
        height: 12px;
        width: 12px;
        margin: 0 5px;
        background-color: #bbb;
        border-radius: 50%;
        display: inline-block;
        transition: background-color 0.3s;
        cursor: pointer;
  
        &.active {
          background-color: #ff6200; // Orange color
        }
      }
    }
  
    @media screen and (max-width: 768px) {
      .carousel {
        width: 100%; // Adjust width for smaller screens
      }
  
      .carousel-inner {
        width: calc(100% * 8); // Adjust width for 8 images, showing 1 at a time on smaller screens
      }
  
      .carousel-item {
        flex: 0 0 calc(100% - 10px); // 1 image per slide on smaller screens, with separation
        margin: 0 5px; // 5px margin on left and right
      }
    }
  }
  