/* footer.scss */
section {
  padding-top: 45px;
}

.footer {
  background-color: #333;
  color: #fff;
  padding: 40px 20px;
}
.footer .footer-content {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 769px) {
  .footer .footer-content {
    flex-direction: column;
  }
}
.footer .footer-content .footer-column {
  flex: 1;
  margin-right: 20px;
}
.footer .footer-content .footer-column .subscribe {
  border-radius: 0.5rem;
  padding: 0.2rem 1.5rem;
  border: 12px;
}
.footer .footer-content .footer-column:last-child {
  margin-right: 0;
}
.footer .footer-content .footer-column h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}
.footer .footer-content .footer-column p {
  font-size: 1rem;
  margin-bottom: 5px;
}
.footer .footer-content .footer-column .icons {
  margin-top: 10px;
}
.footer .footer-content .footer-column .icons .icon:hover {
  color: orangered;
  transition: 0.5s;
}
.footer .footer-content .footer-column .icons svg {
  font-size: 1.5rem;
  margin-right: 10px;
}/*# sourceMappingURL=footer.css.map */