.featured {
  text-align: center;
  padding: 2rem 0;
}
.featured .featured-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: bold;
}
.featured .featured-title span {
  color: #ff6200;
}
.featured .carousel {
  overflow: hidden;
  width: 80%;
  margin: 0 auto;
}
.featured .carousel .carousel-inner {
  display: flex;
  transition: transform 1.5s ease-in-out;
  width: 200%;
}
.featured .carousel .carousel-item {
  flex: 0 0 calc(25% - 10px);
  margin: 0 5px;
  box-sizing: border-box;
}
.featured .carousel .carousel-item img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.featured .carousel-dots {
  margin-top: 1rem;
}
.featured .carousel-dots .dot {
  height: 12px;
  width: 12px;
  margin: 0 5px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.3s;
  cursor: pointer;
}
.featured .carousel-dots .dot.active {
  background-color: #ff6200;
}
@media screen and (max-width: 768px) {
  .featured .carousel {
    width: 100%;
  }
  .featured .carousel-inner {
    width: 800%;
  }
  .featured .carousel-item {
    flex: 0 0 calc(100% - 10px);
    margin: 0 5px;
  }
}/*# sourceMappingURL=featured.css.map */