.contact-container {
    padding: 50px 20px;
    background-color: #fff5e6; // Light orange background
    text-align: center;
  
    .contact-heading {
      font-size: 2.5rem;
      color: #ff6600; // Orange color for the heading
      margin-bottom: 20px;
    }
  
    .contact-form {
      max-width: 600px;
      margin: 0 auto;
      background-color: #fff;
      padding: 20px;
      border-radius: 10px;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  
      .form-group {
        margin-bottom: 20px;
  
        label {
          display: block;
          font-size: 1rem;
          margin-bottom: 5px;
          color: #333;
        }
  
        input,
        textarea {
          width: 100%;
          padding: 10px;
          border: 1px solid #ccc;
          border-radius: 5px;
          font-size: 1rem;
        }
  
        textarea {
          resize: vertical;
        }
      }
  
      .submit-button {
        background-color: #ff6600; // Orange color for the button
        color: #fff;
        padding: 10px 20px;
        border: none;
        border-radius: 5px;
        font-size: 1rem;
        cursor: pointer;
        transition: background-color 0.3s ease;
  
        &:hover {
          background-color: #e55a00; // Darker orange on hover
        }
      }
    }
  }
  
  @media (min-width: 769px) {
    .contact-form {
      padding: 40px;
    }
  }
  