.navbar {
    background: #fff;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: fixed; // Change from relative to fixed
    top: 0;
    width: 100%;
    z-index: 1000; // Ensures it stays above other elements
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); // Optional: Adds a slight shadow for better visibility
}

.navbar-container {
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    width: 100%;
    max-width: 1500px;
    padding: 0 24px;
}

.navbar-logo {
    color: #000;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    font-weight: bold;

    span {
        color: #ff6200; // Orange color
    }
}

.menu-icon {
    display: none;
}

.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
}

.nav-item {
    height: 80px;
}

.nav-links {
    color: #000;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    display: flex;
    align-items: center;

    &:hover {
        color: #ff6200; // Hover text color
    }
}

.nav-menu.active {
    background: #ff6200; // Orange color
    color: #fff;
    width: 250px;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: all 0.3s ease;
    z-index: 999;
}

.menu-icon {
    display: block;
    position: absolute;
    top: 1rem;
    right: 1.5rem;
    font-size: 2rem;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #ff6200; // Orange color
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        &:hover {
            background-color: #ff6200; // Hover background color
            color: #fff; // Change text color on hover
        }
    }

    .menu-icon {
        display: block;
    }
}

@media screen and (min-width: 769px) {
    .menu-icon {
        display: none;
    }
}

// Additional padding to prevent content from being hidden behind the navbar
body {
    padding-top: 80px; // Should match the height of the navbar
}