.contact-container {
  padding: 50px 20px;
  background-color: #fff5e6;
  text-align: center;
}
.contact-container .contact-heading {
  font-size: 2.5rem;
  color: #ff6600;
  margin-bottom: 20px;
}
.contact-container .contact-form {
  max-width: 600px;
  margin: 0 auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.contact-container .contact-form .form-group {
  margin-bottom: 20px;
}
.contact-container .contact-form .form-group label {
  display: block;
  font-size: 1rem;
  margin-bottom: 5px;
  color: #333;
}
.contact-container .contact-form .form-group input,
.contact-container .contact-form .form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}
.contact-container .contact-form .form-group textarea {
  resize: vertical;
}
.contact-container .contact-form .submit-button {
  background-color: #ff6600;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.contact-container .contact-form .submit-button:hover {
  background-color: #e55a00;
}

@media (min-width: 769px) {
  .contact-form {
    padding: 40px;
  }
}/*# sourceMappingURL=contact.css.map */