.home-menu {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}
.home-menu .menu-heading .menu-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: bold;
  margin-bottom: 1rem;
}
.home-menu .menu-heading .menu-title span {
  color: #ff6200;
}
.home-menu .menu-item {
  width: calc(25% - 20px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  background: white;
  border-radius: 10px;
  overflow: hidden;
}
.home-menu .menu-item img {
  width: 100%; /* Ensures the image fills its container */
  height: 200px; /* Adjust height as needed for uniformity */
  -o-object-fit: cover;
     object-fit: cover; /* Ensures images maintain aspect ratio and cover the container */
  border-bottom: 1px solid #ddd;
}
.home-menu .menu-item h3 {
  margin: 10px 0;
  font-size: 1.2rem;
  color: #333;
}
.home-menu .menu-item p {
  font-size: 1rem;
  color: #777;
}
@media (max-width: 769px) {
  .home-menu .menu-item {
    width: calc(50% - 20px); /* Two items per row on smaller screens */
  }
}/*# sourceMappingURL=menu.css.map */