@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

/* Colors for styling*/

:root{
  --PrimaryColor: hsl(199,100%,33%);
  --SecondaryColor: hsl(187,85%,43%);
  --gradientColor: linear-gradient(to right, hsl(187,85%,43%), hsl(199,100%,33%));
  --whiteColor: hsl(0,0%,100%);
  --blackColor: hsl(201,33%,16%);
  --textColor: hsl(240,4%,36%);
  --whiteColorDream: hsl(0,0%,93%);
  --greyText:rgb(190,190,190);
  --inputColor: rgb(239,239,239);
  --bodyColor:rgb(240,240,246);
  --cardBG: rgb(225,225,225);

  /*other customs*/
  --PrimaryColorOrange: hsl(30, 100%, 50%); 
  --SecondaryColorOrange: hsl(15, 100%, 45%);
  --gradientColorOrange: linear-gradient(to right, hsl(15, 100%, 45%), hsl(30, 100%, 50%));

}

h1{
  align-self: center;
  columns: var(--PrimaryColorOrange);
}

.menu-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: bold;
}

a{
  text-decoration: none;
}

Link{
  text-decoration: none;
}

li{
  list-style: none;
}

.section{
  padding: 4rem 0 2rem;
}

.container{
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.icon{
  font-size: 2rem;
  cursor: pointer;
}

.grid{
  display: grid;
  align-items: center;
}

.btn{
  padding: .6rem 1.5rem;
  padding-left: 12px;
  background: var(--SecondaryColorOrange);
  border-radius: 1rem;
  border: none;
  outline: none;
  cursor: pointer;
}

.flex{
  display: flex;
  cursor: pointer;
}

.btn Link{
  color: var(--whiteColor);
  font-weight: 500;
}

.btn a{
  color: var(--whiteColor);
  font-weight: 500;
}

.btn:hover{
  background: var(--PrimaryColorOrange);
}

img, video{
  width: 100%;
  height: auto;
}

input{
  font-size: 100%;
}

body{
  background: var(--bodyColor);
}

span{
  color: var(--whiteColor);
}